<template>
  <div>
    <banner></banner>
    <apresentacao></apresentacao>
    <servicos></servicos>
    <marcar-consulta></marcar-consulta>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./servicos/Banner";
import Apresentacao from "./servicos/Apresentacao";
import Servicos from "./servicos/Servicos";
import MarcarConsulta from "./sobre-nos/Marcar-consulta";

export default {
  components: {
    banner: Banner,
    apresentacao: Apresentacao,
    servicos: Servicos,
    marcarConsulta: MarcarConsulta
  },
  metaInfo() {
    return {
      title: "Serviços - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "Serviços - Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
</style>



